*{margin:0;padding:0;box-sizing:border-box}body{font-family:"Roboto",sans-serif}.container{background-color:#fff;margin:10px}.post-button{text-align:right;margin:15px 0px 0 0}
.conversation {
  display: flex;
  align-items: center;
  padding: 10px;
  cursor: pointer;
  margin-top: 20px;
}

.conversation:hover {
  background-color: rgb(245, 243, 243);
}

.conversationImg {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 20px;
}

.conversationName {
  font-weight: 500;
}

@media screen and (max-width: 768px) {
  .conversationName {
    display: none;
  }
}

.message{display:flex;flex-direction:column;margin-top:20px}.messageTop{display:flex}.messageImg{width:32px;height:32px;border-radius:50%;object-fit:cover;margin:0 10px}.messageText{padding:10px;border-radius:20px;background-color:#f5f1f1;color:#000;max-width:300px}.messageBottom{font-size:12px;margin-top:10px}.message.own{align-items:flex-end}.message.own .messageText{background-color:#00809f;color:#fff}
.chatOnlinePeople {
  display: flex;
  align-items: center;
  font-weight: 500;
  cursor: pointer;
  margin-top: 10px;
}

.chatOnlineImgContainer {
  position: relative;
  margin-right: 10px;
}

.chatOnlineImg {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  object-fit: cover;
  border: 1px solid white;
}

.chatOnlineBadge {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: limegreen;
  position: absolute;
  top: 2px;
  right: 2px;
}

@media only screen and (max-width: 768px) {
  .messenger {
    flex-direction: column;
}
}

.messenger{height:calc(100vh - 70px);display:flex}.chatMenu{flex:3.5 1}.chatMenuInput{width:90%;padding:10px 0;border:none;border-bottom:1px solid gray}.chatBox{flex:5.5 1}.chatBoxWrapper{display:flex;flex-direction:column;justify-content:space-between;position:relative}.chatBoxTop{height:100%;overflow-y:scroll;padding-right:10px}.chatBoxBottom{margin-top:5px;display:flex;align-items:center;justify-content:space-between}.chatMessageInput{width:80%;height:90px;padding:10px}.chatSubmitButton{width:70px;height:40px;border:none;border-radius:5px;cursor:pointer;background-color:teal;color:#fff}.chatOnline{flex:3 1}.chatMenuWrapper,.chatBoxWrapper,.chatOnlineWrapper{padding:10px;height:100%}.noConversationText{position:absolute;top:10%;font-size:50px;color:#e0dcdc;cursor:default}@media screen and (max-width: 768px){.chatMenu{flex:1 1}.chatMenuInput{display:none}.chatBox{flex:10 1}.chatOnline{flex:1px 1}}

